html, body, div, li, label, p, hr {
    margin: 0; padding: 0;
    box-sizing: border-box;
}
html, body {
    background: #fafafa;
    color: #ffffff;
    height: 100%;width: 100%;
}
* {
    box-sizing: border-box;
    user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
    word-break: break-all;
}
*:focus,*:checked,*:active {
    outline: none;
}

a {
    color: inherit;
    text-decoration: none;
    cursor: default;
    -webkit-tap-highlight-color:rgba(0,0,0,0);
}
a:hover, a:active, a:checked {
    outline: none;
    text-decoration: none;
}

.nav-foot > * {
    padding: 15px;
    color: #E4E7EC;
    transition: 0.2s;
    cursor: pointer;
    display: block;
    font-size: 15px;
}
.nav-foot > *:hover {
    color: #FFFFFF;
    background: #de6d6d;
}
.nav-foot > * > .icon {
    width: 24px;
    height: 24px;
    float: right;
    display: block;
}

#nav-publish-popup, #nav-concact-popup {
    position: absolute;
    width: 120px;
    height: auto;
    background: #de6d6d;
    z-index: 1;
}

#nav-publish-popup a, #nav-concact-popup a {
    padding: 15px;
    color: #E4E7EC;
    transition: 0.2s;
    cursor: pointer;
    display: block;
    font-size: 15px;
}

@media screen and (min-width: 1200px) {
    .nav {
        width: 400px;
        background: #cd5c5c;
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        flex-flow: column;
        height: 100%;
        overflow: hidden;
    }
    .nav-body {
        padding-right: 16px;
        flex: 1;
    }
    .nav-body-block {
        padding: 12px;
        display: inline-block;
        border-radius: 4px;
        transition: 0.2s;
        text-align: center;
        margin: 16px 0 0 16px;
    }
    .nav-body-block img {
        height: 70px;width: 70px;
    }
    .nav-body-block:hover {
        background: #de6d6d;
    }
    .nav-foot-block {
        text-align: center;
    }
    .nav-foot-block-title {
        opacity: 0.7;
        text-align: left;
        padding: 24px 16px 16px 16px;
    }
    .nav-foot-block a {
        width: 200px;
        height: 64px;
        line-height: 64px;
        text-align: center;
        color: #fafafa;
        transition: 0.2s;
        display: inline-block;
    }
    .nav-foot-block a:hover {
        color: #ffffff;
        background: #de6d6d;
    }
    .nav-avatar {
        transition: 0.2s;
        height: auto;
        padding: 16px;
        text-align: center;
        background: #cd5c5c;
        width: 100%;
    }
    .nav-avatar img {
        height: 50px;
        width: 50px;
        border-radius: 100px;
    }
    .home-body {
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        height: 100%;
    }
    .home-content {
        flex: 1;
        overflow-y: auto;
        overflow-x: hidden;
    }
    .home-content::-webkit-scrollbar
    {
        width: 2px;
        height: 2px;
    }
    /*定义滚动条轨道 内阴影+圆角*/
    .home-content::-webkit-scrollbar-track
    {
        background: #fafafa;
    }
    /*定义滑块 内阴影+圆角*/
    .home-content::-webkit-scrollbar-thumb
    {
        border-radius: 2px;
        background-color: #ffcfcf;
    }
    .post-block {
        color: #4f4f4f;
        transition: 0.2s;
        display: block;
        float: left;
        padding: 24px;
        width: 25%;
        min-width: 200px;
        height: 120px;
        overflow: hidden;
        background: #ffffff;
        position: relative;
        text-align: left;
    }
    .post-block:hover {
        color: #000000;
        background: #ffefef;
    }
    .post-block::before {
        content: ' ';
        height: 100%;width: 1px;
        position: absolute;
        background: #fafafa;
        right: 0;
        top: 0;
    }
    .post-block::after {
        content: ' ';
        height: 1px;width: 100%;
        background: #fafafa;
        position: absolute;
        left: 0;
        bottom: 0;
    }
    .post-block-title {
        line-height: 1.2;
        font-size: 15px;
    }
    .post-block-desc {
        position: absolute;
        bottom: 24px;left: 24px;
        font-size: 13px;
        opacity: 0.7;
    }
    .article-pager {
        margin: 64px auto;
        width: fit-content;
        border-radius: 8px;
        overflow: hidden;
        max-width: 640px;
        background: #cd5c5c;
    }
    .article-pager > a {
        padding: 0 16px;
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        font-size: 15px;
        height: 80px;
        transition: 0.2s;
    }
    .article-pager > a:hover {
        background: #de6d6d;
    }
    .article-pager img {
        width: 40px;
    }
    .article-pager .article-pager-block-title {
        flex: 1;
        line-height: 80px;
        white-space: nowrap;
        overflow: hidden;
    }
    .article-pager .article-pager-block-tag {
        display: none;
    }
}

