.article {
    color: #4f4f4f;
    max-width: 960px;margin: 0 auto;
    padding: 15px;
    font-size: 15px;
    line-height: 1.5;
}
.article .highlighter-rouge *, code.highlighter-rouge {
    user-select: text;
    -moz-user-select: text;
    -ms-user-select: text;
    -webkit-user-select: text;
}
.article h1 {
    margin: 64px auto;
    width: 70%;
    max-width: 768px;
    min-width: 200px;
}.article h2 {
    margin-bottom: 10px;
    margin-top: 15px;
}
.article h3, .article h4, .article h5, .article h6 {
    margin-bottom: 5px;
    margin-top: 10px;
}
.article ul, .article ol {
    font-weight: 600;
    border-left: 3px solid rgba(255, 255, 255, 0.7);
}
.article ul ul {
    border-left: none;
}
.article ol ul {
    border-left: none;
}
.article ul ol {
    border-left: none;
}
.article ol ol {
    border-left: none;
}
.article li {
    padding: 5px 0;
}
.article p {
    text-indent: 2em;
    margin-top: 5px;
    margin-bottom: 5px;
}
.article img {
    display: block;
    margin: 15px auto;
    max-width: 100%;
}
.article .highlighter-rouge > .highlight {
    overflow-x: auto;
}
.article code.highlighter-rouge {
    background: rgba(250, 250, 210, 0.8);
    color: #000000;
    border-radius: 2px;
    padding: 3px;
    margin-left: 3px;
    margin-right: 3px;
}
.article .highlighter-rouge::selection {
    background: rgba(50, 50, 50, 0.7);
    color: #eeeeee;
    border-radius: 2px;
}
.article .highlighter-rouge::-moz-selection {
    background: rgba(50, 50, 50, 0.7);
    color: #eeeeee;
    border-radius: 2px;
}
.article .highlighter-rouge::-webkit-selection {
    background: rgba(50, 50, 50, 0.7);
    color: #eeeeee;
    border-radius: 2px;
}
.article div.highlighter-rouge {
    margin: 15px 0;
    padding: 10px 15px;
    background: #f3f3f3;
    color: #000000;
    border-radius: 4px;
    white-space: nowrap;
}
.article .highlighter-rouge > .highlight::-webkit-scrollbar
{
    width: 10px;
    height: 10px;
}
/*定义滚动条轨道 内阴影+圆角*/
.article .highlighter-rouge > .highlight::-webkit-scrollbar-track
{
    background: none;
}
/*定义滑块 内阴影+圆角*/
.article .highlighter-rouge > .highlight::-webkit-scrollbar-thumb
{
    height: 10px;width: 10px;
    border-radius: 2px;
    background: rgba(0, 0, 0, 0.5);
}
.article table {
    margin: 15px auto;
    display: block;
    width: 100%;
    overflow: auto;
    border-spacing: 0;
    border-collapse: collapse;
}
.article tr {
    border-top: 1px solid #c6cbd1;
    background: rgba(50, 50, 50, 0.5);
}
.article tr:nth-child(2n) {
    background: rgba(0, 0, 0, 0.5);
}
.article th {
    font-weight: 600;
    padding: 6px 13px;
    border: 1px solid #dfe2e5;
}
.article td {
    padding: 6px 13px;
    border: 1px solid #dfe2e5;
}
.article code {
    font-weight: 500;
}
.article a {
    color: #0099ff;
    text-decoration: underline;
    cursor: pointer;
}

.article .language-shell.highlighter-rouge {
    background: #4f4f4f;
    color: #ffffff;
}

/* code part */
.article .language-javascript code .kd,.language-javascript  code .k {
    color: #0000ff;
}
.article .language-javascript code .s1 {
    color: #a31515;
}
.article .language-javascript code .mi {
    color: #09885a;
}
.article .language-javascript code .c1,.language-javascript  code .cm {
    color: #008000;
}

.article .language-json code .s2 {
    color: #a31515;
}
.article .language-json code .kc {
    color: #0000ff;
}

.article .language-typescript code .kd,.language-typescript  code .k {
